.page {
  box-sizing: border-box;

  &::before,
  &::after {
    box-sizing: inherit;
  }

  &__body {
    margin: 0;
    font-family: "Arial", sans-serif;
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    color: $color-dark-grey;
    background-color: $color-white;

    @media (min-width: $tablet-width) {
      font-size: 16px;
      line-height: 24px;
    }
  }
}
