.nav {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: $color-white;
  z-index: 2;

  &--shown {
    display: block;
  }

  @media (min-width: $tablet-width) {
    width: auto;
    position: relative;
    top: auto;
    display: flex;
  }

  @media (min-width: $tablet-width) {
    background-color: transparent;
  }

  &__list {
    padding: 0;
    margin: 0;
    list-style-type: none;
    border-top: 1px solid #e6e6e6;

    @media (min-width: $tablet-width) {
      display: flex;
      flex-wrap: wrap;
      gap: 34px;
      border-top: none;
    }
  }

  &__item {
    border-bottom: 1px solid #e6e6e6;

    @media (min-width: $tablet-width) {
      border-bottom: none;
    }
  }

  &__link {
    position: relative;
    display: block;
    padding: 22px 0 24px;
    position: relative;
    font-family: $font-secondary;
    font-size: 20px;
    font-weight: 500;
    line-height: 20px;
    text-decoration: none;
    text-transform: uppercase;
    color: $color-black;
    text-align: center;

    @media (min-width: $tablet-width) {
      font-size: 18px;
      line-height: 24px;
      font-weight: 400;
    }

    @media (min-width: $desktop-width) {
      font-size: 20px;
      line-height: 30px;
      font-weight: 400;
      color: $color-white;
    }

    &--current::before {
      @media (min-width: $tablet-width) {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 16px;
        height: 2px;
        background-color: $color-basic;
      }

      @media (min-width: $desktop-width) {
        background-color: $color-white;
      }
    }

    &--color-black {
      color: #111111;

      &.nav__link--current::before {
        background-color: $color-basic;
      }
    }
  }

  &__button {
    border: none;
    padding: 0;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }
}
